
.el-loading-mask.is-fullscreen .el-loading-spinner i{color:#ffffff;font-size:50px
}
.el-loading-mask.is-fullscreen .el-loading-spinner .el-loading-text{color:#ffffff;margin:8px 0;font-size:22px
}
.apiface{height:100%
}
.apiface .normal{height:100%;position:relative
}
.apiface .normal .view-main:not(.not-show-left-body){padding-left:5px !important
}
.apiface .normal .view-main{overflow:auto;padding-left:283px;height:100%
}
.left-right-position-box .dash-panel{position:absolute;width:100%;height:100%;top:0;left:0;z-index:-99
}
.left-right-position-box .arrow-icon{cursor:pointer;width:12px;height:40px;position:absolute;top:50%;margin-top:-20px;background-size:100% 100%;background-repeat:no-repeat
}
.left-right-position-box.left-position-box .arrow-icon{display:none;right:-12px;margin-top:-20px;background-image:url("../icons/expand_arrow.png")
}
.left-right-position-box.left-position-box .arrow-icon:hover{background-image:url("../icons/expand_arrow_active.png")
}
.left-right-position-box.left-position-box .arrow-icon.shrink{background-image:url("../icons/shrink_arrow.png")
}
.left-right-position-box.left-position-box .arrow-icon.shrink:hover{background-image:url("../icons/shrink_arrow_active.png")
}
.left-right-position-box.left-position-box.not-show-left .arrow-icon{display:block
}
.left-right-position-box.left-position-box:hover .arrow-icon{display:block
}
.left-right-position-box.right-position-box .arrow-icon{display:none;left:-12px;background-image:url("../icons/expand_arrow_r.png")
}
.left-right-position-box.right-position-box .arrow-icon:hover{background-image:url("../icons/expand_arrow_active_r.png")
}
.left-right-position-box.right-position-box .arrow-icon.shrink{background-image:url("../icons/shrink_arrow_r.png")
}
.left-right-position-box.right-position-box .arrow-icon.shrink:hover{background-image:url("../icons/shrink_arrow_active_r.png")
}
.left-right-position-box.right-position-box:hover .arrow-icon{display:block
}
.left-position-box{width:280px;height:100%;position:absolute;top:0;left:0;z-index:99;transition:left .2s linear
}
.left-position-box.not-show-left{left:-278px
}
.left-position-box .left-position-content{width:100%;height:100%;border-right:1px solid #e6e6e6;position:relative
}


