
.addr-select-item .item__title{float:left;width:95px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis
}
.addr-select-item .item__addr{float:right;color:#8492a6;font-size:8px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;width:260px;text-align:right
}
