
.self-menu{height:100%;position:relative;padding-top:43px;background:#545c64
}
.self-menu .scrollbar-wrapper{overflow-x:hidden !important
}
.self-menu .scrollbar-wrapper .el-scrollbar__view{height:100%
}
.self-menu .no-menu{padding-top:300px;color:#e2e6ec;text-align:center
}
.self-menu .search-menu{top:0;position:absolute;width:100%;padding:4px 12px
}
.self-menu .search-menu .el-input__prefix{font-size:14px !important;left:2px !important;color:#e2e6ec !important
}
.self-menu .search-menu .el-input--small,.self-menu .search-menu .el-input--small .el-input__inner{height:35px !important;line-height:35px !important;vertical-align:top
}
.self-menu .search-menu input[type="text"]{background:#545c64;border:0;color:#fff
}
.self-menu .self-menu__inner{height:100%;overflow-y:auto
}
.self-menu .self-menu__inner::-webkit-scrollbar{width:0 !important
}
.self-menu .self-menu__inner .el-menu{width:99%;min-height:100%;border-right:solid 0 #e6e6e6 !important
}
.self-menu .self-menu__inner .el-menu .el-menu-item-group__title,.self-menu .self-menu__inner .el-menu .el-menu-item,.self-menu .self-menu__inner .el-menu .el-submenu__title{font-size:14px !important;height:38px !important;line-height:38px !important
}
.self-menu .self-menu__inner .el-menu .el-menu-item.is-active{background-color:#434a50 !important
}


