
.time-info{padding:5px;border-left:1px solid #cfd9e5;border-right:1px solid #cfd9e5;border-top:1px solid #cfd9e5
}
.time-info .time-info-title{padding:8px 16px;background-color:#ecf8ff;border-radius:4px;border-left:5px solid #50bfff;margin-bottom:5px;color:#87afcc;font-size:14px;font-weight:700
}
.time-info:first-child{border-radius:5px 5px 0 0
}
.time-info:last-child{border-radius:0 0 5px 5px;border-bottom:1px solid #eaeaef
}
.time-info .el-col{line-height:25px;height:25px;font-size:14px;font-weight:600;color:#5893c2
}
.time-info .el-col:not(.el-col-params){color:#666;text-align:right
}


