
.v-modal{background:#18365e
}
.el-dialog__wrapper{overflow:hidden
}
.self-dialog{margin:0 auto;height:96%;max-height:600px;background-color:#f2f4f6;border:1px solid #dddddd
}
.self-dialog.el-dialog--full{height:100%;max-height:100%
}
.self-dialog .el-dialog__header{padding:0 16px;height:43px;line-height:42px;border-bottom:1px solid #cbcbcb
}
.self-dialog .el-dialog__header .el-dialog__title{font-size:14px;color:#333333;font-weight:bold
}
.self-dialog .el-dialog__header .el-dialog__headerbtn{font-size:14px;top:14px;color:#999999
}
.self-dialog .el-dialog__header .el-dialog__headerbtn:hover{color:#1177e2
}
.self-dialog .el-dialog__header .el-dialog__headerbtn .el-dialog__close{color:#9e9e9e
}
.self-dialog .el-dialog__header .el-dialog__headerbtn .el-dialog__close:hover{color:#4b70a4
}
.self-dialog .el-dialog__body{width:100%;padding:4px 0;height:calc(100% - 94px);overflow:hidden
}
.self-dialog .el-dialog__body .el-scrollbar{height:calc(100% + 17px)
}
.self-dialog .el-dialog__body .el-scrollbar__wrap{padding:10px;height:100%
}
.self-dialog .el-dialog__body .el-scrollbar__bar{bottom:17px
}
.self-dialog .el-dialog__body .__rail-is-vertical{right:4px !important
}
.self-dialog .el-dialog__footer{text-align:center;position:absolute;width:100%;height:52px;box-sizing:border-box;bottom:0;left:0;padding:10px 20px;border-radius:2px;border-top:1px solid #cbcbcb
}
.self-dialog .el-dialog__footer .self-button{min-width:90px;height:30px;line-height:28px;margin-left:14px
}
.self-dialog-nofooter .el-dialog__body{width:100%;padding:0;height:calc(100% - 38px)
}


