
.responses-info-doc{border-left:1px solid #cfd9e5;border-right:1px solid #cfd9e5;border-top:1px solid #cfd9e5
}
.responses-info-doc:first-child{border-radius:5px 5px 0 0
}
.responses-info-doc .bg{background:#f7f7fc
}
.responses-info-doc span.title{line-height:28px;font-size:14px;font-weight:700;border-bottom:1px solid #eaeaef;padding:5px 0 5px 24px;display:-ms-flexbox;display:flex
}
.responses-info-doc span.title.c200{color:#67c23a
}
.responses-info-doc span.title.c204{color:#909399
}
.responses-info-doc span.title.c401{color:#f56c6c
}
.responses-info-doc span.title.c50x{color:#f56c6c
}
.responses-info-doc span.title.c403{color:#e6a23c
}
.responses-info-doc .schema-data{padding:10px;background:#f7f7fc;height:390px
}
.responses-info-doc .schema-data .code-view{overflow:auto;width:25%;float:left;height:100%
}
.responses-info-doc .schema-data .code-view pre{margin-top:0;margin-bottom:0;font-family:SFMono-Regular,Consolas,"Liberation Mono",Menlo,Courier,monospace;color:#d255c5
}
.responses-info-doc .schema-data .datagrid{float:right;width:74%
}
.responses-info-doc .schema-data .datagrid .el-table__body-wrapper{height:333px;overflow-y:auto;overflow-x:hidden
}
.responses-info-doc .simple-data{font-size:13px;font-weight:600;height:66px;line-height:66px;padding-left:70px;color:#5893c2
}
.responses-info-doc .no-data{color:#999;line-height:66px;text-align:center
}
.responses-info-doc-more{border-radius:0 0 5px 5px;border:1px solid #cfd9e5;padding:6px;text-align:center;color:#555
}
.responses-info-doc-more .doc-more-font:hover{color:#5893c2
}
.responses-info-doc-more .doc-more-font{cursor:pointer
}
.responses-info-doc-more i{padding-left:3px
}


