
.fieldset-box{padding:5px 12px 12px;border:1px solid #ccc;border-radius:5px;background-color:#fff
}
.fieldset-box legend{display:inline-block;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;color:#bcc2cd;font-weight:800;-webkit-padding-start:5px;padding-inline-start:5px;-webkit-padding-end:5px;padding-inline-end:5px;padding-bottom:3px
}
.fieldset-box legend *{vertical-align:middle
}
.fieldset-box legend i{cursor:pointer;padding-left:3px
}
.fieldset-box.purple{border:1px solid #c1aae1
}
.fieldset-box.purple legend{color:#bd93f9
}
.fieldset-box.orange{border:1px solid #eecba6
}
.fieldset-box.orange legend{color:#ffb86c
}
.fieldset-box.pink{border:1px solid #f6badd
}
.fieldset-box.pink legend{color:#ff79c6
}
.fieldset-box.blue{border:1px solid #a7daf8
}
.fieldset-box.blue legend{color:#57b5f9
}
.fieldset-box .collapsible.hidden{display:none
}


