
.array-format-tips-title{width:68px;line-height:18px;display:-ms-inline-flexbox;display:inline-flex;color:#4b71a4
}
.request-section{width:calc(100% - 42px);overflow:hidden
}
.request-section .el-table__inner-self{padding:0 0 0 120px
}
.request-section .el-table__inner-self .el-table__header-wrapper{display:none
}
.request-section .collapsible{width:100%
}
.request-section .el-table__header,.request-section el-table__body{width:100% !important
}
.request-section .cell{height:100% !important;padding-bottom:1px;padding-top:1px;line-height:31px
}
.request-section .cell .el-table__expand-icon{padding-top:30px
}
.request-section .el-textarea.el-input--small{height:100% !important
}
.request-section .el-textarea.el-input--small textarea{resize:none;height:100%;white-space:nowrap
}
.request-section .datagrid-pagination-box{overflow:hidden;background-color:#fff;z-index:99;width:100%;height:37px;line-height:36px;border-color:#dddddd;border-style:solid;border-width:0 1px 1px 1px;text-align:left;padding-left:22px;box-sizing:border-box
}
.request-section .datagrid-pagination-box .array-format-tips{padding-left:6px
}
.request-section .datagrid-pagination-box .array-format-tips i{font-size:14px;color:#4b71a4
}
.request-section .datagrid-pagination-box .array-format-select{padding:0 6px;float:left;border:1px solid #ced8e5;color:#4d4d4d;height:23px;line-height:23px;cursor:pointer;margin-left:-1px
}
.request-section .datagrid-pagination-box .array-format-select:first-child{margin-left:0
}
.request-section .datagrid-pagination-box .array-format-select.current-format{background-color:#4b71a4;color:#ffffff
}
.request-section .datagrid-pagination-box .array-format{padding-left:18px;padding-top:6px
}
.request-section .datagrid-pagination-box .btn-group{font-size:0;float:right;padding:5px 10px
}
.request-section .datagrid-pagination-box .btn-group .el-button{padding:0;margin-left:8px;padding-left:8px;border-left:1px solid #cbcbcb;height:17px
}
.request-section .datagrid-pagination-box .btn-group .el-button:first-child{margin-left:0;padding-left:0;border-left:none
}


