
.toolbox{position:fixed;z-index:10;bottom:10px;right:10px;width:40px;height:40px;display:-ms-flexbox;display:flex;-ms-flex-pack:end;justify-content:flex-end;-ms-flex-align:end;align-items:flex-end;transition:all .2s ease
}
.toolbox .bg_links{width:40px;height:40px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;background-color:rgba(0,0,0,0.2);border-radius:100%;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);position:absolute
}
.toolbox .mores{width:40px;height:40px;z-index:9;background-image:url(svg/logo_white.svg);background-size:50%;background-repeat:no-repeat;background-position:10px 7px;opacity:.9;transition:all 1s .2s ease;bottom:0;right:0
}
.toolbox .social{opacity:0;right:0;bottom:0
}
.toolbox .social .icon{width:100%;height:100%;background-size:20px;background-repeat:no-repeat;background-position:center;background-color:transparent;display:-ms-flexbox;display:flex;transition:all .2s ease,background-color .4s ease;opacity:0;border-radius:100%
}
.toolbox .social.portfolio{transition:all .8s ease
}
.toolbox .social.portfolio .icon{background-image:url(svg/link.svg)
}
.toolbox .social.dribbble{transition:all .3s ease
}
.toolbox .social.dribbble .icon{background-image:url(svg/dribbble.svg)
}
.toolbox .social.linkedin{transition:all .8s ease
}
.toolbox .social.linkedin .icon{background-image:url(svg/linkedin.svg)
}
.toolbox:hover{width:105px;height:105px;transition:all .6s cubic-bezier(.64, .01, .07, 1.65)
}
.toolbox:hover .mores{opacity:1;transition:all .6s ease
}
.toolbox:hover .social{opacity:1
}
.toolbox:hover .social .icon{opacity:.9
}
.toolbox:hover .social:hover{background-size:28px
}
.toolbox:hover .social:hover .icon{background-size:65%;opacity:1
}
.toolbox:hover .social.portfolio{right:0;bottom:calc(60%);transition:all .3s 0s cubic-bezier(.64, .01, .07, 1.65)
}
.toolbox:hover .social.portfolio .icon:hover{background-color:#698fb7
}
.toolbox:hover .social.dribbble{bottom:45%;right:45%;transition:all .3s .15s cubic-bezier(.64, .01, .07, 1.65)
}
.toolbox:hover .social.dribbble .icon:hover{background-color:#ea4c89
}
.toolbox:hover .social.linkedin{bottom:0;right:calc(60%);transition:all .3s .25s cubic-bezier(.64, .01, .07, 1.65)
}
.toolbox:hover .social.linkedin .icon:hover{background-color:#0077b5
}


